// eslint-disable-next-line no-undef
class QtQuick_Flickable extends QtQuick_Item {
    static enums = {
        Flickable: { 
            AutoFlickDirection: 0,
            AutoFlickIfNeeded: 1,
            HorizontalFlick: 2,
            VerticalFlick: 3,
            HorizontalAndVerticalFlick: 4,
        }
      };
    static properties = {
        contentHeight: "real",
        contentWidth: "real",
        contentX: "real",
        contentY: "real",
        flickableDirection: { type: "enum", initialValue: 0 },
        interactive: { type: "bool", initialValue: true },
    };
    static signals = {
        completed: [],
        flickStarted: [],
        flickEnded: [],
    };
  
    constructor(meta) {
      super(meta);
        this.parent = meta.parent;
        this.dom.style.pointerEvents = "all";
        this.dom.style.overflow = "auto";

        const bg = this.impl = document.createElement("div");
        bg.style.pointerEvents = "none";
        bg.style.position = "absolute";
        bg.style.left = bg.style.right = bg.style.top = bg.style.bottom = "0px";
        bg.style.background = "none";
        this.dom.appendChild(bg);


        this.isDragging = false;
        this.mouseIsDown = false;
        this.curSX = 0;
        this.curSY = 0;
        this.dom.addEventListener("mousedown", (e) => {
            e.stopPropagation();
            e.preventDefault();
            this.mouseIsDown = true;
            this.curSX = e.pageX;
            this.curSY = e.pageY;
            //this.dom.classList.add("unselectable");
        });
        this.dom.addEventListener("mouseup", (e) => {
            //e.stopPropagation();
            e.preventDefault();
            setTimeout(() => {this.$onAllMouseEvent()}, 100);
            this.isDragging = false;
            this.mouseIsDown = false;
            this.dom.classList.remove("unselectable");
        });
        this.dom.addEventListener("mouseleave", (e) => {
            e.stopPropagation();
            e.preventDefault();
            setTimeout(() => {this.$onAllMouseEvent()}, 100);
            this.isDragging = false;
            this.mouseIsDown = false;
            this.dom.classList.remove("unselectable");
        });
        this.dom.addEventListener("mousemove", (e) => {
            e.stopPropagation();
            e.preventDefault();
            if (this.mouseIsDown && this.enabled) {
                this.dom.classList.add("unselectable");
                this.$offAllMouseEvent();
                this.isDragging = true;

                if(this.interactive){
                    switch(this.flickableDirection){
                        case Flickable.AutoFlickDirection:
                            if(this.contentHeight != this.height) this.dom.scrollBy(0, this.curSY - e.pageY);
                            if(this.contentWidth != this.width) this.dom.scrollBy(this.curSX - e.pageX, 0);
                            break;
                        case Flickable.AutoFlickIfNeeded:
                            if(this.contentHeight >= this.height) this.dom.scrollBy(0, this.curSY - e.pageY);
                            if(this.contentWidth >= this.width) this.dom.scrollBy(this.curSX - e.pageX, 0);
                            break;
                        case Flickable.HorizontalFlick:
                            this.dom.scrollBy(this.curSX - e.pageX, 0);
                            break;
                        case Flickable.VerticalFlick:
                            this.dom.scrollBy(0, this.curSY - e.pageY);
                            break;
                        case Flickable.HorizontalAndVerticalFlick:
                            this.dom.scrollBy(this.curSX - e.pageX, this.curSY - e.pageY);
                            break;
                    }
                    this.contentX = this.dom.scrollLeft;
                    this.contentY = this.dom.scrollTop;
                    this.curSX = e.pageX;
                    this.curSY = e.pageY;
                }
            }
        });
        this.dom.addEventListener("wheel", (e) => {
            e.stopPropagation();
            e.preventDefault();
            this.mouseIsWheel = true
            if(this.interactive){
                
                this.$calcContentXY(e.deltaX, e.deltaY);
            }
            this.mouseIsWheel = false
        });
        this.dom.addEventListener("scroll", (e) => {
            e.stopPropagation();
            e.preventDefault();
          });

        this.contentHeightChanged.connect(this, this.$contentHeightChanged);
        this.contentWidthChanged.connect(this, this.$contentWidthChanged);
        this.contentXChanged.connect(this, this.$contentXChanged);
        this.contentYChanged.connect(this, this.$contentYChanged);
        this.flickableDirectionChanged.connect(this, this.$flickableDirectionChanged);

        this.Component.completed.connect(this, this.Component$onCompleted);
    }
    Component$onCompleted(){
        this.$flickableDirectionChanged();
        this.$contentHeightChanged();
        this.$contentWidthChanged();
        this.dom.scrollTo(this.contentX, this.contentY);
    }
    $calcContentXY(deltaX, deltaY){
        if(this.enabled){

            let p = this.$firstParentForWheel()
            var limitH = this.contentHeight//Math.max(this.dom.clientHeight, this.dom.scrollHeight, this.dom.offsetHeight);
            var limitW = this.contentWidth//Math.max(this.dom.clientWidth, this.dom.scrollWidth, this.dom.offsetWidth);

            if (this.orientation === Qt.Horizontal) {
            if(this.contentX + deltaX > limitW - this.width) {
                this.contentX = limitW - this.width;
                if(p) {
                if(this.mouseIsDown) p.enabled = true
                p.$calcContentXY(deltaX, deltaY)
                }
            } else
            if(this.contentX + deltaX < 0) {
                this.contentX = 0;
                if(p) {
                if(this.mouseIsDown) p.enabled = true
                p.$calcContentXY(deltaX, deltaY)
                }
            } else this.contentX += deltaX;
            } else {
            if(this.contentY + deltaY > limitH - this.height) {
                this.contentY = limitH - this.height;
                if(p) {
                if(this.mouseIsDown) p.enabled = true
                p.$calcContentXY(deltaX, deltaY)
                }
            } else
            if(this.contentY + deltaY < 0) {
                this.contentY = 0;
                if(p) {
                if(this.mouseIsDown) p.enabled = true
                p.$calcContentXY(deltaX, deltaY)
                }
            } else this.contentY += deltaY;
            }
        }
    }
    $flickableDirectionChanged(){
        this.dom.style.overflow = "hidden";
        switch(this.flickableDirection){
            case Flickable.AutoFlickDirection:
                if(this.contentHeight != this.height) this.dom.style.overflowY = "auto";
                if(this.contentWidth != this.width) this.dom.style.overflowX = "auto";
                break;
            case Flickable.AutoFlickIfNeeded:
                if(this.contentHeight >= this.height) this.dom.style.overflowY = "auto";
                if(this.contentWidth >= this.width) this.dom.style.overflowX = "auto";
                break;
            case Flickable.HorizontalFlick:
                this.dom.style.overflowX = "auto";
                break;
            case Flickable.VerticalFlick:
                this.dom.style.overflowY = "auto";
                break;
            case Flickable.HorizontalAndVerticalFlick:
                this.dom.style.overflow = "auto";
                break;

        }
    }
    $contentHeightChanged(){
        if(this.contentHeight === 0){
            this.impl.style.height = `100%`;
            this.impl.style.bottom = `0`;
        } else {
            this.impl.style.height = `${this.contentHeight}px`;
            this.impl.style.bottom = `${this.contentHeight}px`;
        }
    }
    $contentWidthChanged(){
        if(this.contentWidth === 0){
            this.impl.style.width = `100%`;
            this.impl.style.right = `0`;
        } else {
            this.impl.style.width = `${this.contentWidth}px`;
            this.impl.style.right = `${this.contentWidth}px`;
        }
        
    }
    $contentXChanged(){
        if(this.mouseIsDown || this.mouseIsWheel){
            if(this.eventTimer) clearTimeout(this.eventTimer); else this.flickStarted();
            this.eventTimer = setTimeout(()=>{
                this.flickEnded();
                this.eventTimer = null;
            }, 100)
        }
        this.dom.scrollTo(this.contentX, this.contentY);
    }
    $contentYChanged(){
        if(this.mouseIsDown || this.mouseIsWheel){
            if(this.eventTimer) clearTimeout(this.eventTimer); else this.flickStarted();
            this.eventTimer = setTimeout(()=>{
                this.flickEnded();
                this.eventTimer = null;
            }, 100)
        }
        this.dom.scrollTo(this.contentX, this.contentY);
    }
  }
  QmlWeb.registerQmlType(QtQuick_Flickable);
