// eslint-disable-next-line no-undef
class QtQuick_TextEdit extends QtQuick_Item {
  static enums = {
    TextEdit: { 
      AlignLeft: Qt.AlignLeft,
      AlignRight: Qt.AlignRight,
      AlignHCenter: Qt.AlignHCenter,
      AlignTop: Qt.AlignTop,
      AlignBottom: Qt.AlignBottom,
      AlignVCenter: Qt.AlignVCenter, 
      NoWrap: 0,
      WordWrap: 1,
      WrapAnywhere: 2,
      Wrap: 3,
    }
  };
  static properties = {
    activeFocusOnPress: { type: "bool", initialValue: true },
    baseUrl: "url",
    canPaste: "bool",
    canRedo: "bool",
    canUndo: "bool",
    color: { type: "color", initialValue: "white" },
    contentHeight: "real",
    contentWidth: "real",
    cursorDelegate: "Component",
    cursorPosition: "int",
    cursorRectangle: "rect",
    cursorVisible: { type: "bool", initialValue: true },
    effectiveHorizontalAlignment: "enum",
    font: "font",
    hoveredLink: "string",
    inputMethodComposing: "bool",
    inputMethodHints: "enum",
    length: "int",
    lineCount: "int",
    mouseSelectionMode: "enum",
    persistentSelection: "bool",
    readOnly: { type: "bool", initialValue: false },
    renderType: "enum",
    selectByKeyboard: { type: "bool", initialValue: true },
    selectByMouse: "bool",
    selectedText: "string",
    selectedTextColor: { type: "color", initialValue: "yellow" },
    selectionColor: { type: "color", initialValue: "pink" },
    selectionEnd: "int",
    selectionStart: "int",
    text: "string",
    textDocument: "TextDocument",
    textFormat: "enum",
    textMargin: "real",
    wrapMode: { type: "enum", initialValue: 0 },
    horizontalAlignment: "enum",
    verticalAlignment: { type: "enum", initialValue: Qt.AlignTop },
  };
  static signals = {
    linkActivated: [{ type: "string", name: "link" }],
    linkHovered: [{ type: "string", name: "link" }]
  };

  constructor(meta) {
    super(meta);

    // Undo / Redo stacks;
    this.undoStack = [];
    this.undoStackPosition = -1;
    this.redoStack = [];
    this.redoStackPosition = -1;

    this.dom.style.overflow = "auto"
    this.dom.style.pointerEvents = "all";
    const textarea = this.impl = document.createElement("div");
    textarea.style.display = "flex"
    textarea.style.pointerEvents = "all";
    textarea.style.width = "100%";
    textarea.style.minHeight = "100%";
    textarea.style.boxSizing = "border-box";
    textarea.style.borderWidth = "0";
    textarea.style.background = "none";
    textarea.style.outline = "none";
    textarea.style.overflow = "auto";
    textarea.style.flexWrap = "wrap";
    //textarea.classList.add('TextEdit')
    //textarea.style.resize = "none";
    textarea.contentEditable = true;
    textarea.style.padding = "0"; // TODO: padding/*Padding props from Qt 5.6
    // In some browsers text-areas have a margin by default, which distorts
    // the positioning, so we need to manually set it to 0.
    textarea.style.margin = "0";
    textarea.disabled = false;
    this.dom.appendChild(textarea);

    this.Component.completed.connect(this, this.Component$onCompleted);
    this.textChanged.connect(this, this.$onTextChanged);
    this.colorChanged.connect(this, this.$onColorChanged);
    this.horizontalAlignmentChanged.connect(this, this.$alignChanged);
    this.verticalAlignmentChanged.connect(this, this.$alignChanged);
    this.wrapModeChanged.connect(this, this.$wrapModeChanged);

    this.impl.addEventListener("input", () => this.$updateValue());
    this.impl.addEventListener("click", (e) => {
      this.impl.focus()
    });
  }
  
  append(text) {
    this.text += text;
  }
  copy() {
    // TODO
  }
  cut() {
    //this.text = this.text(0, this.selectionStart) +
                //this.text(this.selectionEnd, this.text.length);
    // TODO
  }
  deselect() {
    //this.selectionStart = -1;
    //this.selectionEnd = -1;
    //this.selectedText = null;
    // TODO
  }
  getFormattedText(start, end) {
    //const text = this.text.slice(start, end);
    // TODO
    // process text
    //return text;
  }
  getText(start, end) {
    //return this.text.slice(start, end);
  }
  insert(/*position, text*/) {
    // TODO
  }
  isRightToLeft(/*start, end*/) {
    // TODO
  }
  linkAt(/*x, y*/) {
    // TODO
  }
  moveCursorSelection(/*x, y*/) {
    // TODO
  }
  paste() {
    // TODO
  }
  positionAt(/*x, y*/) {
    // TODO
  }
  positionToRectangle(/*position*/) {
    // TODO
  }
  redo() {
    // TODO
  }
  remove(/*start, end*/) {
    // TODO
  }
  select(/*start, end*/) {
    // TODO
  }
  selectAll() {
    // TODO
  }
  selectWord() {
    // TODO
  }
  undo() {
    // TODO
  }
  $wrapModeChanged(){
    
    switch(this.wrapMode){
      case TextEdit.NoWrap:
        this.impl.style.wordBreak = 'normal'
        this.impl.style.whiteSpace = 'nowrap'
        break;
      case TextEdit.WordWrap:
        this.impl.style.wordBreak = 'break-word'
        this.impl.style.whiteSpace = 'break-spaces'
        break;
      case TextEdit.WrapAnywhere:
        this.impl.style.wordBreak = 'break-all'
        this.impl.style.whiteSpace = 'break-spaces'
        break;
      case TextEdit.Wrap:
        this.impl.style.wordBreak = 'break-all'
        this.impl.style.whiteSpace = 'break-spaces'
        break;
    }
  }
  $alignChanged(){

    switch(this.horizontalAlignment){
      case TextEdit.AlignLeft:
        this.impl.style.justifyContent = 'flex-start';
        break;
      case TextEdit.AlignRight:
        this.impl.style.justifyContent = 'flex-end';
        break;
      case TextEdit.AlignHCenter:
        this.impl.style.justifyContent = 'center';
        break;
    }
    switch(this.verticalAlignment){
      case TextEdit.AlignTop:
        this.impl.style.alignItems = 'flex-start';
        break;
      case TextEdit.AlignBottom:
        this.impl.style.alignItems = 'flex-end';
        break;
      case TextEdit.AlignVCenter:
        this.impl.style.alignItems = 'center';
        break;
    }

    this.impl.style.padding = "0"
    if(this.text == ""){
      
      switch(this.horizontalAlignment){
        case TextEdit.AlignLeft:
          //this.impl.style.justifyContent = 'flex-start';
          break;
        case TextEdit.AlignRight:
          this.impl.style.paddingLeft = `${this.parent.width}px`;
          break;
        case TextEdit.AlignHCenter:
          this.impl.style.paddingLeft = `${this.parent.width/2}px`;
          break;
      }
      switch(this.verticalAlignment){
        case TextEdit.AlignTop:
          //this.impl.style.alignItems = 'flex-start';
          break;
        case TextEdit.AlignBottom:
          this.impl.style.paddingTop =  `${this.parent.height - this.font.pixelSize/2}px`;
          break;
        case TextEdit.AlignVCenter:
          this.impl.style.paddingTop =  `${this.parent.height/2 - this.font.pixelSize/2}px`;
          break;
      }
    }

  }
  Component$onCompleted() {
    this.selectByKeyboard = !this.readOnly;
    
    this.impl.readOnly = this.readOnly;
    
    this.$updateValue();
    this.implicitWidth = this.offsetWidth;
    this.implicitHeight = this.offsetHeight;
  }
  $onTextChanged(newVal) {
    //this.impl.value = newVal;
    //console.log(this.text)
    if(this.text !== this.impl.innerHTML){
      this.impl.innerHTML = this.text
    }
    this.$alignChanged()
  }
  $onColorChanged(newVal) {
    this.impl.style.color = newVal.$css;
  }
  $updateValue() {
    if (this.text !== this.impl.innerHTML) {
      this.text = this.impl.innerHTML;
    }
    /*
    this.length = this.text.length;
    this.lineCount = this.$getLineCount();
    this.$updateCss();

    this.contentHeight = this.impl.scrollHeight;*/
  }
  // Transfer dom style to firstChild,
  // then clear corresponding dom style
  $updateCss() {
    const supported = [
      "border",
      "borderRadius",
      "borderWidth",
      "borderColor",
      "backgroundColor",
    ];
    const style = this.impl.style;
    for (let n = 0; n < supported.length; n++) {
      const o = supported[n];
      const v = this.css[o];
      if (v) {
        style[o] = v;
        this.css[o] = null;
      }
    }
  }
  $getLineCount() {
    return this.text.split(/\n/).length;
  }
}
QmlWeb.registerQmlType(QtQuick_TextEdit);
